export default {
	data() {
		return {
			labelIndex: 0, // 当前评论类型下标
			ratingsList: [],
			videoContext: null, // 播放器对象
			videoDirection: 0, // 全屏方向
			showFullscreenBtn: true, // 是否全屏
			videoSrc: '',
			showVideo: true, // 小程序端隐藏
			clicked_list:[false,false,false,false,false] ,//对应星星个数
			showNull : false
		}
	},
	onLoad() {
		try {
			const comments = uni.getStorageSync('comments'); //读取本地存储
			if (comments) {
				comments.forEach((item) => {
				  item.time =  this.timestampToTime(item.time)
				})
				this.ratingsList = comments;
				if(this.ratingsList.length < 1){
					this.showNull = true;
					console.log('显示')
				}
				else{
					this.showNull = false;
				}
				
			}
		} catch (e) {
			// error
		}

		this.showVideo = false

	},
	onReady: function(res) {
		this.videoContext = uni.createVideoContext('myVideo')
	},
	methods: {
		loadRatings(index) {
			this.labelIndex = index;
		},
		showBigImg(src, srclist) { // 预览图片
			uni.previewImage({
				current: src,
				urls: srclist
			});
		},
		playVideo(path) { // 播放视频
			this.videoSrc = path;
			this.$nextTick(function() {
				this.videoContext.requestFullScreen({
					direction: 0
				});
			});

			this.showVideo = true;

		},
		videoPause() {
			this.videoSrc = '';

			this.showVideo = false;

		},
		viderFullscreen(e) {
			if (e.detail.fullScreen) { // 全屏
				this.videoContext.play();
			} else { // 退出全屏
				this.videoContext.pause();
			}
		},
		videoError(e) {
			console.log(e)
		},
		timestampToTime(cjsj) {
			let time = cjsj.toString()
			if(time.length == 10){
				time = time * 1000
			}
			var date = new Date(time) //时间戳为10位需*1000，时间戳为13位的话不需乘1000
			var Y = date.getFullYear() + '-'
			var M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-'
			var D = date.getDate() + ' '
			var h = date.getHours() + ':'
			var m = date.getMinutes() + ':'
			var s = date.getSeconds()
			return Y + M + D + h + m + s
		}
	}
}
